*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    

}
@media screen and (min-width: 1440px) {
    *{
        margin: 0;
        padding: 0;
        box-sizing: border-box;
        font-size: 24px;
    }
}

body{
    background: black;
}
.App {
    width: 23.4375rem;
    height: 23rem;
    display: block;
    margin: 2rem auto;
    border-radius: 1rem;
    background: #ffb838;
    border: 0.25rem solid #feea09;
}

.cardBox{
    display: flex;
    flex-wrap: wrap;
    padding: 0.5rem 0 0 0.5rem;
}

.shownDispay {
    opacity: 1;
;
}

.hiddenDisplay {
    opacity: 0;
}

.card {
    width: 7rem;
    height: 19rem;
    background: #000000;
    margin-right: 0.4rem;
    border-radius: 1rem;
    overflow: hidden;
    color: #feea09;
    padding-top: 0.5rem;
    
}

.card__pile {
    width: 7rem;
    height: 9rem;
    font-size: 2rem;
    border: 0;
    background: #000000;
    cursor: pointer;
    transition: 0.5s ease-in-out;
}
.card__pile:hover {
    transform: scale(1.05);
    filter: brightness(120%);
    transition: 0.2s ease-in-out;
}


.card__pile-img{
    width: 7rem;
    height: 9rem;
}

.stackControl{
    margin-top: 1rem;
    text-align: center;
}

.quantity {
    display: block;
    width: 4rem;
    margin: 0.8rem auto 0.8rem auto;
    background: #ffb838;
    border: 0;
    padding-left: 0.5rem;

}

.stack__btn{
    width: 4rem;
    height: 2.5rem;
    background: #ffb838;
    border: 0.25rem solid #feea09;
    cursor: pointer;
    transition: 0.5s ease-in-out;
}

.stack__btn:hover{
    transform: scale(1.1);
    filter: brightness(120%);
    transition: 0.2s ease-in-out;
}


.result{
    width: 22rem;
    height: 20rem;
    background: #000000;
    color: #ffb838;
    border: 0.25rem solid #feea09;
    display: block;
    margin: auto;
    position: relative;
    top: -18rem;
    text-align: center;
    padding: 5rem 2rem 2rem 2rem;
    font-size: 2rem;
    -webkit-box-shadow: 0px 0px 50rem 15rem #000000; 
    box-shadow: 0px 0px 50rem 15rem #000000;
}

.result__btn{
    display: inline-block;
    width: 5rem;
    margin: 2rem 1rem;
    background: #ffb838;
    border: 0.25rem solid #feea09;
    cursor: pointer;
    transition: 0.5s ease-in-out;
}

.result__btn:hover{
    transform: scale(1.1);
    filter: brightness(120%);
    transition: 0.2s ease-in-out;
}

.logWindow{
    margin: 0.5rem 0 0 0.5rem;
    width: 21.8rem;
    padding: 0.5rem;
    height: 2rem;
    background: #000000;
    color: #feea09;
    text-align: center;
    border-radius: 1rem;

}

.instructions{
    display: block;
    position: absolute;
    top: 26rem;
    width: 23rem;
    padding: 1rem;
    border-radius: 1rem;
    text-align: center;
    background: #ffb838;
    border: 0.25rem solid #feea09;
}

.instructions__list{
    margin: 1rem;

}